import { useEffect } from 'react';

const Page = () => {
  useEffect(() => {
    const fetchData = async () => {
      try {
        const urlParams = new URLSearchParams(window.location.hash.substring(1));
        const accessToken = urlParams.get('access_token');

        if (accessToken) {
          const validate = await validateToken(accessToken);
          const data = await getUser(validate.user_id);

          const loginData = {
            access_token: accessToken,
            client_id: validate.client_id,
            id: data.id,
            login: data.login,
            description: data.description,
            profile_image_url: data.profile_image_url
          };

          localStorage.setItem('login', JSON.stringify(loginData));

          window.open(`${window.location.origin}/dashboard`, '_self');
        } else {
          window.open(`${window.location.origin}/dashboard`, '_self');
        }
      } catch (err) {
        console.error(err);
      }
    };

    fetchData();
  }, []);

  const validateToken = async (token) => {
    try {
      const validateResponse = await fetch('https://id.twitch.tv/oauth2/validate', {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });

      if (!validateResponse.ok) {
        throw new Error('Invalid access token');
      }

      const validateData = await validateResponse.json();
      return validateData;
    } catch (err) {
      console.error(err);
    }
  };

  const getUser = async (id) => {
    try {
      const response = await fetch(`https://api.twitch.tv/helix/users?id=${id}`, {
        headers: {
          'Client-ID': '9uavest5z7knsvpbip19fxqkywxz3ec'
        }
      });

      if (!response.ok) {
        throw new Error('Error getting user');
      }

      const data = await response.json();
      return data.data[0];
    } catch (err) {
      console.error(err);
    }
  };

  return null;
};

export default Page;
